<template>
  <div class="article-editor">
    <el-row style="margin-bottom: 5px;">
      <el-col :span="12" class="top">
        <span style="width: 80px">标题名：</span>
        <el-input style="width: 200px" v-model="articleForm.title" placeholder="请输入内容"></el-input>
      </el-col>
      <el-col :span="12" class="opt">
        <el-button size="mini" type="primary" plain icon="el-icon-thumb" @click="saveArticle">保存</el-button>
      </el-col>
    </el-row>
    <div>
      <!-- 编辑器-->
      <cherry-components ref="cherryComponents"
                         :height="height"
                         :editorModel="editorModel"
      ></cherry-components>
    </div>
  </div>
</template>

<script>

import CherryComponents from "@/components/Cherry/CherryComponents";
import {insertArticle} from '@/api/document/pianzi'

export default {
  name: "EditorIndex",
  components: {
    CherryComponents
  },
  data() {
    return {
      articleForm: {
        title: '',
        content: ''
      },
      // 编辑模式
      editorModel: 'editOnly',
      // 自定义编辑器高度
      height: '800px'
    }
  },
  methods: {
    /**
     * 保存文章
     */
    saveArticle() {
      this.articleForm.content = this.$refs.cherryComponents.cherryInstance.getValue()
      insertArticle(this.articleForm).then(res => {
        if (res && res.data === true) {
          this.$message.success('保存成功');
          this.resetInfo();
        } else {
          this.$message.error('保存失败');
        }
      })
    },
    /**
     * 清空内容
     */
    resetInfo(){
      this.articleForm.title = '';
      this.$refs.cherryComponents.cherryInstance.setValue('');
    }
  }
}
</script>

<style scoped>
.article-editor {
  width: 1000px;
  margin: 0 auto;
  height: 100%;
  border: 1px solid grey;
  padding: 5px;
  border-radius: 5px;
}

.top {
  display: flex;
  height: 40px;
  line-height: 40px
}

.opt {
  text-align: right;
  padding-right: 5px;
}
</style>